import React from "react";
import { useSearchParams } from "react-router-dom";
import styled from "styled-components";
import useAxios from "../api/Axios";
import { useDispatch, useSelector } from "react-redux";
import { reset } from "../reduxjs/ReloadSlice";
import { MarksProvider } from "../Context/MarksEntry";
import { setNavBarTitle } from "../reduxjs/TitleSlice";
import { useAuth } from "../Context/Auth";
import { FaChevronLeft, FaChevronRight } from "react-icons/fa";
import { AddStudentProvider } from "../Context/AddStudent";
import { ExportProvider } from "../Context/Export";

const TableForMark = React.lazy(() =>
  import("../components/assets/TableForMarks")
);
const TopInformationPanel = React.lazy(() =>
  import("../components/assets/TopInformationPanel")
);

const ToolsBox = React.lazy(() =>
  import("../components/assets/MarksEntry/ToolsBox")
);
const Class = () => {
  const [Params] = useSearchParams();
  const axios = useAxios();
  const dispatch = useDispatch();
  const auth = useAuth();
  const [classInfo, setClassInfo] = React.useState(null);
  const [loading, setLoading] = React.useState(true);
  const { flag } = useSelector((state) => state.reload);

  React.useEffect(() => {
    auth.handleDashboard(false);
    getClass();
    if (flag) {
      dispatch(reset());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [Params, axios, flag, dispatch]);

  const getClass = async () => {
    try {
      const url = "/view?id=" + Params.get("id");
      await axios.get(url).then((res) => {
        setClassInfo(res.data);
        dispatch(setNavBarTitle(res.data[0].courseName));
      });
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  const handleScroll = (dir) => {
    const content = document.getElementById("scroll");
    const contentScrollWidth = content.scrollWidth;

    if (dir === "left") {
      let scrollLeft = content.scrollLeft + 100;
      if (scrollLeft >= contentScrollWidth) {
        scrollLeft = contentScrollWidth;
      }
      content.scrollLeft = scrollLeft;
    } else {
      let scrollRight = content.scrollLeft - 100;
      if (scrollRight <= 0) {
        scrollRight = 0;
      }
      content.scrollLeft = scrollRight;
    }
  };

  return (
    <MarksProvider>
      <AddStudentProvider>
        <ExportProvider>
          <Section>
            {loading ? (
              "Loading..."
            ) : (
              <Container>
                {/*
                 * Course Name
                 * Semester
                 * Session
                 * Teacher Name
                 */}
                <TopInformationPanel
                  classInfo={classInfo[0]}
                  onReloadPage={getClass}
                />
                <ToolsBox classInfo={classInfo[0]} />
                <BodyPanel id="scroll">
                  {/* * Marks Table View */}
                  <TableForMark classInfo={classInfo[0]} />

                  <ScrollButton onClick={() => handleScroll("left")}>
                    <FaChevronRight />
                  </ScrollButton>

                  <ScrollButton onClick={() => handleScroll("right")}>
                    <FaChevronLeft />
                  </ScrollButton>
                </BodyPanel>
              </Container>
            )}
          </Section>
        </ExportProvider>
      </AddStudentProvider>
    </MarksProvider>
  );
};

export default Class;

export const Section = styled.section`
  height: calc(100vh - 80px);
  margin-top: 0px;
  width: 100%;
`;
export const Container = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem 1rem;
  width: 100%;
`;

export const InfoPanel = styled.div`
  position: relative;
  padding: 01rem;
  width: 100%;
  border-radius: 5px;
  color: #fff;
  height: 200px;
  margin-bottom: 0.5rem;
`;

export const Title = styled.h1`
  font-size: 1.2rem;
`;

export const SubTitle = styled.h5`
  font-size: 1rem;
`;

export const OptionPanel = styled.div`
  position: absolute;
  right: 5px;
  top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 35px;
  height: 35px;
  font-size: 1.4rem;
  cursor: pointer;
  &:hover {
    background: rgba(240, 240, 240, 0.4);
  }
`;

export const OptionItems = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 35px;
  top: 15px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 4px rgba(3, 3, 3, 0.2);
`;

export const Li = styled.li`
  color: #333;
  padding: 0.3rem 0.5rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;

  &:nth-last-child(1) {
  }
`;

const BodyPanel = styled.div`
  width: 100%;
  overflow-x: scroll;
  padding-bottom: 1rem;
  border: 1px solid rgba(3, 3, 3, 0.3);
  position: relative;
`;

const ScrollButton = styled.span`
  background: rgba(3, 3, 3, 0.5);
  width: 50px;
  height: 50px;
  border-radius: 50px;
  top: 0;
  bottom: 0;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: #333;

  &:nth-child(1) {
    left: 1rem;
  }

  &:nth-child(2) {
    right: 1rem;
  }

  margin: auto 0;
  cursor: pointer;
  color: #fff;
  &:hover {
    background: rgba(3, 3, 3, 0.8);
  }
`;
